// Import
/* eslint-disable */
import Vue from 'vue'
import 'jquery'
import App from './App.vue'
import router from './router'
import i18n from './locales'
import VueMeta from 'vue-meta'
import store from './store'
import Notifications from 'vue-notification'
import vuetify from './vuetify'
import 'bootstrap'
import '@/assets/boxicons/css/boxicons.min.css'
import '@/assets/icofont/icofont.min.css'
import '@/assets/animate.css/animate.min.css'
import '@/assets/style.scss'



// Register layouts
const authLayout = () => import('./layouts/auth')
const adminLayout = () => import('./layouts/admin')
const mainLayout = () => import('./layouts/main')
Vue.component('authLayout', authLayout)
Vue.component('adminLayout', adminLayout)
Vue.component('mainLayout', mainLayout)

// Config
Vue.config.productionTip = false
Vue.prototype.$backendUrl = process.env.VUE_APP_BACKEND_URL
Vue.prototype.$appName = process.env.VUE_APP_NAME
Vue.use(Notifications)
Vue.use(VueMeta)

// Start Vue Js
new Vue({
    el: '#app',
    router,
    i18n,
    store,
    vuetify,
    render: h => h(App)
}).$mount('#app')