/* eslint-disable */
import httpAxios from '@/httpAxios.js'
export default {
    name: 'Login-Module',

	metaInfo: function() {
		return {
			title: this.$t('auth.login'),
		}
	},

	computed: {
	  passwordMatch() {
		return () => this.password === this.verify || "Password must match";
	  }
	},

	data() {
		return {
			loginData: {
				email: '',
				password: '',
			},
			errors: {
				email: false,
				password: false,
			},
			valid : true,
			dialog: true,
			tab: 0,
			tabs: [
				{name:"Login", icon:"mdi-account"},
				{name:"Register", icon:"mdi-account-outline"}
			],
			firstName: "",
			lastName: "",
			email: "",
			password: "",
			verify: "",
			loginPassword: "",
			loginEmail: "",
			loginEmailRules: [
			  v => !!v || "Required",
			  v => /.+@.+\..+/.test(v) || "E-mail must be valid"
			],
			emailRules: [
			  v => !!v || "Required",
			  v => /.+@.+\..+/.test(v) || "E-mail must be valid"
			],
		
			show1: false,
			rules: {
			  required: value => !!value || "Required.",
			  min: v => (v && v.length >= 8) || "Min 8 characters"
			}
		}
	},

    methods: {
        login: async function() {
			const self = this
			self.$store.commit('DISPLAY_LOADER',true)
			// Clear Errors
            Object.keys(this.errors).forEach(function(key) {
                self.errors[key] = false
            });
			
			// Ajax Request
			await httpAxios({
                url: this.$backendUrl + '/login',
                method: 'POST',
                data: self.loginData
            }).then(response => {
				self.$store.commit('LOGGED_USER', response.data)
				
				self.$router.push({ name: 'adminDashboard' })
			})
			.catch(error => {
				try {
					if(error.response.status == 422) {
						for(var errorKey in error.response.data.errors) {
							if(errorKey in self.errors) {
                                self.errors[errorKey] = true
                            }
						}
					}
				} catch(e) {
					console.log(e)
				}
			})
			self.$store.commit('DISPLAY_LOADER',false)
        },

		reset() {
		  this.$refs.form.reset();
		},

		resetValidation() {
		  this.$refs.form.resetValidation();
		}
	},
}